.header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
}
.nav-items>ul{
    display: flex;
    list-style: none;
}
.nav-items>ul>li{
    padding: 20px;
}
.logo{
    width: 100px;
}
.card{
    width: 200px;
    border: 1px solid black;
    padding: 10px;
    margin: 10px;
   
}
.card>img{
    width: 100%;
    height: 180px;
}
.lists{
    display: flex;
    flex-wrap: wrap;
}
.shimmer {
    height: 180px;
    background: linear-gradient(to right, rgba(255, 0, 0, 0) 0%, rgb(252, 0, 0) 50%, rgba(212, 12, 12, 0) 100%);
    animation: shimmer 2s infinite;
    background-size: 200% 100%; /* Makes the gradient large enough to slide */
  }
  
  @keyframes shimmer {
    0% {
      background-position: -200% 0;
    }
    100% {
      background-position: 200% 0;
    }
  }